* {
  font-family: "Rubik", sans-serif;
  margin: 0%;
  --teel: teal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.App {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* header */

/* to toggle nav bar */
.showNav-enter {
  margin-top: 0vh;
  transition: all 1000ms ease-in-out;
}

/* to toggle nav bar */
.hideNav-enter {
  margin-top: -10vh;
  transition: all 1000ms ease-in-out;
}

header {
  background-color: black;
  color: white;
  height: 10vh;
  width: 100%;
  margin: auto;
  position: fixed;
  display: flex;
  z-index: 10;
  cursor: pointer;
}

h1.HG {
  font-weight: 900;
  font-family: "Braah One", sans-serif;
  letter-spacing: 5px;
  font-size: 9vh;
  padding: 3px;
  padding-left: 30px;
}
.teel {
  padding: 0% !important;
  color: var(--teel);
}

header ul {
  width: 100%;
  padding-left: 0%;
  border-radius: 100px;
  display: flex;
  background-color: transparent;
}

header li {
  color: white;
  list-style: none;
  background-color: transparent;
  margin: auto;
  font-weight: 900;
}

li:hover {
  /* animation: rubberBand linear 1s; */
  color: var(--teel);
}

/* FrontPage */

.frontPage {
  display: flex;
  align-items: center;
  height: 100vh;
  /* border: 3px solid red; */
  justify-content: space-around;
}

.profile {
  margin-top: 10vh;
  width: 40%;
  box-sizing: border-box;
  box-shadow: 1px 1px 1px #919191, 1px 2px 1px #919191, 1px 3px 1px #919191,
    1px 4px 1px #919191, 1px 5px 1px #919191, 1px 6px 1px #919191,
    1px 7px 1px #919191, 1px 8px 1px #919191, 1px 9px 1px #919191,
    1px 10px 1px #919191, 1px 18px 6px rgba(16, 16, 16, 0.4),
    1px 22px 10px rgba(16, 16, 16, 0.2), 1px 25px 35px rgba(16, 16, 16, 0.2),
    1px 30px 60px rgba(16, 16, 16, 0.4);
  overflow: hidden;
  border: 3px solid #bfbfbf;
  border-radius: 100%;
  animation-duration: 8s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: rotate;
  /* animation: rubberBand linear 1s; */
}

.profile:hover {
  border-color: teal;
}

@keyframes rotate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

.textContainer {
  /* border: 3px solid black; */
  width: fit-content;
}

.textContainer .headingChar {
  font-size: 6rem;
  color: white;
  font-family: "Braah One", sans-serif;
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
  text-shadow: 1px 1px 1px #919191, 1px 2px 1px #919191, 1px 3px 1px #919191,
    1px 4px 1px #919191, 1px 5px 1px #919191, 1px 6px 1px #919191,
    1px 7px 1px #919191, 1px 8px 1px #919191, 1px 9px 1px #919191,
    1px 10px 1px #919191, 1px 18px 6px rgba(16, 16, 16, 0.4),
    1px 22px 10px rgba(16, 16, 16, 0.2), 1px 25px 35px rgba(16, 16, 16, 0.2),
    1px 30px 60px rgba(16, 16, 16, 0.4);
}

.rubber {
  animation: rubberBand linear 1s;
}

.rubber:hover {
  color: var(--teel) !important;
}

@keyframes rubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, 0.95, 1);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

/* about */



section {
  height: 100vh;
}

.comparisonSection {
  position: relative;
  /* padding-bottom: 56.25%; to maintain aspect ratio (responsive!) */
  overflow-x: hidden;
  /* border: 3px solid red; */
  right: 1px;
}
.comparisonImage {
  width: 100%;
  height: 100%;
}
.comparisonImage img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* overflow: hidden; */
  position: absolute;
  top: 0;
}

.beforeImage img{
  filter: blur(4px) grayscale(1);
}

.afterImage {
  position: absolute;
  overflow: hidden;
  top: 0;
  transform: translate(100%, 0px);
}
.afterImage img {
  transform: translate(-100%, 0px);
  filter: blur(1px);
}


/* universal */

a {
  background-color: inherit;
  color: inherit;
  text-decoration: none;
}

/* responsive */
@media screen and (max-width: 1000px) {
  .frontPage {
    flex-direction: column;
  }

  .profile {
    width: 50%;
    aspect-ratio: 1/1.5;
  }

  .contactContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
  .contactPage a {
    font-size: 1.3rem;
    width: 70%;
    margin-top: 5%;
  }
}

@media screen and (max-width: 660px) {
  .headingChar {
    font-size: 4rem !important;
  }

  .profile {
    width: 70%;
    aspect-ratio: 1/1.5;
  }
}

@media screen and (max-width: 420px) {
  header {
    width: 99%;
    left: 0%;
    font-size: 10px !important;
  }
}
